.rectangle {
  position: absolute;
  border: 1px solid #1469e1;
}
.rectangle .trash.disabled {
  z-index: 0;
}

.rectangle .trash {
  z-index: 1;
  top: 0;
  left: 100%;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  position: absolute;
  transform: translateX(10px);
  background-color: #fdfffb;
}

.rectangle .dot {
  width: var(--DotSize);
  height: var(--DotSize);
  position: absolute;
  background-color: #1469e1;
  transform: translate(-50%, -50%);
}

.rectangle .dot:nth-of-type(1) { left: 0; top: 0; }
.rectangle .dot:nth-of-type(2) { left: 0; top: 50%;}
.rectangle .dot:nth-of-type(3) { left: 0; top: 100%; }
.rectangle .dot:nth-of-type(4) { left: 50%; top: 0; }
.rectangle .dot:nth-of-type(5) { left: 50%; top: 100%; }
.rectangle .dot:nth-of-type(6) { left: 100%; top: 0; }
.rectangle .dot:nth-of-type(7) { left: 100%; top: 50%; }
.rectangle .dot:nth-of-type(8) { left: 100%; top: 100%; }